  addAndupdateFun(funcan, callback) {
    let vm = this;
    let eachData = funcan.eachData; //
    let UrlApi = funcan.UrlApi; //api
    // 
    let listDataApi = funcan.list.UrlApi;
    let data = funcan.list.data;
    let esData = funcan.list.esData;
    axios({
      method: "post",
      url: api[UrlApi],
      data: eachData
    }).then(function (res) {
      if (res.data.code == 200) {
        let showMsg = res.data.data;
        if (res.data.msg) showMsg = res.data.msg;
        Message({
          message: showMsg,
          type: "success"
        });
        funcan.closeDialog = false; //diglog
        callback && callback();
        if (listDataApi) {
          vm.initData(listDataApi, data, esData);
        }
      }
    });
  },
  //
  examineFun(funcan, callback, { editType, that } = {}) {
    let vm = this;
    let eachData = funcan.eachData; //
    let examineApi = funcan.examineApi;
    let esData = funcan.list.esData;
    let checkShow = funcan.checkShow;
    // 
    let UrlApi = funcan.list.UrlApi;
    let data = funcan.list.data;
    MessageBox.confirm("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      customClass: editType && that.autoTest && that.autoTest(editType),
      type: "warning"
    })
      .then(() => {
        axios({
          method: "post",
          url: api[examineApi],
          data: eachData
        }).then(function (res) {
          if (res.data.code == 200) {
            let showMsg = res.data.data;
            if (res.data.msg) showMsg = res.data.msg;
            if (res.data.data.length == 0) {
              Message({
                message: showMsg,
                type: "success"
              });
            } else {
              Message({
                message: showMsg,
                type: "success"
              });
            }
            checkShow = true;
          }
          if (callback) {
            setTimeout(() => {
              callback && callback()
            }, 500)
          } else {
            setTimeout(function () {
              vm.initData(UrlApi, data, esData);
            }, 800);
          }
        });
      })
      .catch(() => {
        Message({
          message: "",
          type: "info"
        });
      });
  },
