export default {
  methods: {
    initEdit() {
      // 
      var selectConfig = {
        showHeader: true,
        reserve: true,
        checkMethod: event => { // 
          return this.rowClass(event, true);
        }
      };
      this.select_Config = Object.assign(selectConfig, this.selectConfig)
      // 
      var editConfig = {
        trigger: "click",
        mode: "cell",
        showStatus: false,
        showIcon: true,
        activeMethod: this.isEdit, // 
        autoClear: false //  
      };
      this.edit_Config = Object.assign(editConfig, this.editConfig);
    }
  },
}