<template>
  <!--  -->
  <el-select v-if="show" clearable v-model="scope.row[itemObj['id']]" placeholder="" @focus="getList(scope)" @change="value => {changeFn(scope, list, value)}" :loading="loading">
    <el-option v-for="(item, index) in list" :label="item[itemObj['name']]" :value="item[itemObj['id']]" :key="item.index">
    </el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      loading: false,
      show: false
    };
  },
  props: {
    // 
    vxeParams: {
      type: Object,
      default: () => {}
    },
    // 
    scope: {
      type: Object,
      default: () => {}
    },
    // focus 
    url: {
      type: String,
      default: "getWarehouseFocus"
    },
    //  select change
    changeFn: {
      type: Function,
      default: function() {}
    },
    // for 
    itemObj: {
      type: Object,
      default: () => {
        return {
          id: "warehouse_id",
          name: "warehouse_name"
        };
      }
    }
  },
  created() {
    this.getList(this.scope);
  },
  methods: {
    // 
    getList(scope) {
      let { row } = scope
      if (this.list.length > 0) {
        return;
      }
      this.loading = true;
      let params = Object.assign(
        {},
        {
          ...this.vxeParams
        }
      );
      this.$axMethod[this.url](params)
        .then(res => {
          this.loading = false;
          if (res.data.code === 200) {
            this.list = res.data.data;
            this.show = true;
          }
        })
        .catch(error => {
          this.loading = false;
        });
    }
  }
};
</script>

