export default { // 
  methods: {
    // 
    cellClick(scope) {
      this.$emit('cellClick', scope)
    },
    // 
    cellContextMenuEvent({
      row
    }) {
      this._proxy('setCurrentRow', row)
    },
    // 
    cellHover(scope) {
      // if (!this.hoverCurrentRow) {
      //   setTimeout(() => {
      //     this.hoverCurrentRow = scope.row;
      //     this.currentChangeEvent(scope);
      //     
      // this._proxy('setCurrentRow', scope.row)
      //   }, 50);
      // }
      this.$emit('cell-mouseenter', scope)
    },
    // 
    currentChangeEvent(scope) {
      // this.currentRdit = scope.row;
      // this.currentRditColumn = [];
      // scope.$table.tableColumn.forEach(val => {
      //   if (val.editRender) {
      //     this.currentRditColumn.push(val.property);
      //   }
      // });
      // this.currentRdit = scope.row;
      this.$emit('current-change', scope)
    },
    // 
    cellDblclick(scope) {
      this.$emit("cellDblclick", scope);
    },
    // 
    radioChange(row) {
      this.$emit("radioChange", row);
    },
    //
    editActived(scope) {
      this.$emit("editActived", scope.row);
    },
    // 
    selectChange(row) {
      row.selection = row.records
      this.$emit("selectChange", row);
      //  
      if (this.$parent.handleSelectionChange) {
        this.$parent.handleSelectionChange(row.records);
      }
    },
    // 
    sizeChange(size) {
      this.$emit("sizeChange", size);
      if (this.$parent.pageSize) {
        this.$parent.pageSize = size;
      }
    },
    // rowclass
    rowClass({
      row,
      $rowIndex
    }, step) {// step === true 
      // step
      if (this.sign.arr) {
        for (var i = 0; i < this.sign.arr.length; i++) {
          if (this.sign.arr[i][this.sign.str] === row[this.sign.str]) {//  noDisabled == true 
            if (step && this.rowClassName.isDisable && this.sign.noDisabled !== true) {
              return false;
            } else {
              return this.rowClassName.name;
            }
          }
        }
      }
      for (var j = 0; j < this.indexSign.length; j++) {
        if ($rowIndex + this.indexSignStart === this.indexSign[j] - 0) {
          if (step && this.indexSignConfig.indexDisabled) {
            return false;
          } else {
            return this.indexSignConfig.className ? this.indexSignConfig.className : "sign_row";
          }
        }
      }
      if (step) {
        return true;
      }
    },
    // 
    pageChange(row) {
      this.$emit("pageChange", row.currentPage);
      if (this.$parent.goPage) {
        this.$parent.goPage = row.currentPage;
      }
    }
  }
}