// 0
const checkNumPot2 = (rule, value, callback) => {
  const reg = /(^[0-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
  if (!value && value !== 0) {
    return callback(new Error(""));
  } else if (!reg.test(value)) {
    return callback(new Error("0,2"));
  } else {
    callback();
  }
};
const checkNum = (rule, value, callback) => {
  if (isNaN(value)) {
    return callback(new Error(""));
  } else if (
    value.toString().split(".")[1] ?
      value.toString().split(".")[1].length > 2 :
      false
  ) {
    return callback(new Error("2"));
  } else {
    callback();
  }
};
// // 
const checkIdNum = (rule, value, callback) => {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
  if (!reg.test(value)) {
    return callback(new Error(""));
  } else {
    callback();
  }
};

// 
const checkInterNum = (rule, value, callback) => {
  const reg = /^[0-9]*[1-9][0-9]*$/;
  if (!value) {
    return callback(new Error(""));
  } else if (!reg.test(value)) {
    return callback(new Error(""));
  } else {
    callback();
  }
};
//
const telephone = (rule, value, callback) => {
  const reg = /^([0-9]{3,4}-)?[0-9]{7,8}$/;
  if (!reg.test(value) && value != undefined && value != "") {
    return callback(new Error(',"-"'));
  } else {
    callback();
  }
};
//400-010-1231

const kfphone = (rule, value, callback) => {
  const reg = /^400(-\d{3,4}){2}$/;
  if (!reg.test(value) && value != undefined && value != "") {
    return callback(new Error(',"400-xxx-xxx"'));
  } else {
    callback();
  }
};
//400-010-1231

const bank_numberys = (rule, value, callback) => {
  const reg = /^[0-9]*[1-9][0-9]*$/;
  if (!value) {
    return callback(new Error(""));
  } else if (!reg.test(value)) {
    return callback(new Error(""));
  } else {
    callback();
  }
};

//
const kong = (rule, value, callback) => {
  if (!value) {
    return callback(new Error(""));
  } else {
    callback();
  }
};
//

const emailNew = (rule, value, callback) => {
  const reg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,5}$/;
  if (!reg.test(value)) {
    return callback(
      new Error("4~18@xxx.com/cn")
    );
  } else {
    callback();
  }
};
const purchasePrice = (rule, value, callback) => {
  if (!value) {
    return callback(new Error(""));
  } else if (value < 0) {
    return callback(new Error("0"));
  } else {
    callback();
  }
};

