export default {
  methods: {
    initTool() {
      // 
      var tool = {
        id: "Jybd",
        buttons: [
          // {
          //   name: "",
          //   dropdowns: [
          //     // { code: "input", name: ".csv" },
          //     { code: "export", name: ".csv" }
          //   ]
          // }
        ],
        // refresh: true, // 
        zoom: true,
        resizable: {
          storage: true
        },
        setting: {
          // 
          storage: true, // 
          immediate: true // 
        }
      };
      // id //
      if (this.$router) {
        var route = this.$router.currentRoute.fullPath.split("/");
        if (route[3]) {
          if (route[3].indexOf('?') !== -1) {
            tool.id = route[3].slice(0, route[3].indexOf('?') - 1)
          } else {
            tool.id = route[3]
          }
        } else {
          tool.id = route[2]
        }
        if (this.isShowTool) {
          this.tool_Config = Object.assign(tool, this.toolConfig);
        }
      }
    }
  },
}