import axios from "common/utils/axios";

export default {
  // 
  serviceStationGetSalesOutList: '/service_station/sales/SalesOut/getSalesOutList',
  serviceStationGetSalesOutDetail: '/service_station/sales/SalesOut/salesOutDetail',
  serviceStationGetSalesOutGoodsList: '/service_station/sales/SalesOut/getGoodsList',
  // 
  serviceStationAddSalesOut(data) {
    return axios({
      url: "/service_station/sales/SalesOut/addOut",
      method: "post",
      data: data
    });
  },
  // 
  serviceStationAuidtorPassSalesOut(data) {
    return axios({
      url: "/service_station/sales/SalesOut/auidtorPass",
      method: "post",
      data: data
    });
  },
  // 
  serviceStationSalesOutPayPrice(data) {
    return axios({
      url: "/service_station/sales/SalesOut/payPrice",
      method: "post",
      data: data
    });
  },
  // 
  serviceStationGetAllClassAndBrand(data) {
    return axios({
      url: "/service_station/open/Goods/getAllClassAndBrand",
      method: "get",
      params: data
    });
  },
  // 
  serviceStationaBolishSalesOut: '/service_station/sales/SalesOut/abolish',
  // serviceStationaBolishSalesOut(data) {
  //   return axios({
  //     url: "/service_station/sales/SalesOut/abolish",
  //     method: "get",
  //     params: data
  //   });
  // },
  // 
  serviceStationGetDeliveryDemandList(data) {
    return axios({
      url: "/open/DeliveryDemand/getDeliveryDemandList",
      method: "get",
      params: data
    });
  },
  // 
  serviceStationGetDeliveryPrintSalesOutInfo: '/service_station/Sales/SalesOut/printDelivery',
  // 
  serviceStationGetDeliveryGetSalesOutInfo(data) {
    return axios({
      url: "/service_station/sales/SalesOut/getSalesOutInfo",
      method: "get",
      params: data
    });
  },
  // 
  serviceStationExportGoodsListUrl(data) {
    return axios({
      url: "/service_station/sales/SalesOut/exportGoodsListUrl",
      method: "get",
      params: data
    });
  },
}