// 
export const Debounce = (fn, t) => {
  let delay = t || 500;
  let timer;
  return function () {
    let args = arguments;
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, args);
    }, delay);
  }
};
// 
export const Throttle = (fn, t) => {
  let last;
  let timer;
  let interval = t || 500;
  return function () {
    let args = arguments;
    let now = +new Date();
    if (last && now - last < interval) {
      clearTimeout(timer);
      timer = setTimeout(() => {
        last = now;
        fn.apply(this, args);
      }, interval);
    } else {
      last = now;
      fn.apply(this, args);
    }
  }
};

const formatData = (data) => { // YY-MM-DD
  if (data instanceof Date) {
    var year = data.getFullYear();
    var month = (data.getMonth() + 1).toString();
    var day = data.getDate().toString();
    month = month.length == 2 ? month : '0' + month;
    day = day.length == 2 ? day : '0' + day;

    return year + '-' + month + '-' + day;
  }
}
const formatDataYear = (data) => { // YYYY
  if (data instanceof Date) {
    var year = data.getFullYear();
    return year;
  }
}

const get3MonthBefor = () => {
  var resultDate, year, month, date;
  var currDate = new Date();
  year = currDate.getFullYear();
  month = currDate.getMonth() + 1;
  date = currDate.getDate();
  switch (month) {
    case 1:
    case 2:
    case 3:
      month += 9;
      year--;
      break;
    default:
      month -= 3;
      break;
  }
  month = (month < 10) ? ('0' + month) : month;
  resultDate = year + '-' + month + '-' + date;
  return resultDate;
}


/** 
 * @referDate   yyyy-MM-DD
 * @date  yyyy-MM-DD
 * */
function getNextMonth(referDate, date) {
  var getReferDate = new Date(referDate),
    getReferYear = getReferDate.getFullYear(), // 
    getReferMonth = getReferDate.getMonth() + 1, // 
    getReferDay = getReferDate.getDate(); // 
  var getReferDays = new Date(getReferYear, getReferMonth, 0).getDate(); // 

  var getEndDate = new Date(date), // 
    year = getEndDate.getFullYear(), // 
    month = getEndDate.getMonth() + 1; // 
  // day = getEndDate.getDate(); // 

  var year2 = year; // 
  var month2 = parseInt(month) + 1; // 
  if (month2 > 12) { // 12
    year2 = parseInt(year2) + 1; // 1
    month2 = 1; // 1
  }
  var days = new Date(year2, month2, 0).getDate(); // 
  var day2 = days;
  if (getReferDay >= getReferDays) { //  
    day2 = days; // 
  } else { // 
    if (getReferDay < days) { //     
      day2 = getReferDay
    } // 
  }
  if (month2 < 10) { // 
    month2 = '0' + month2;
  }
  if (day2 < 10) { // 
    day2 = '0' + day2;
  }
  var t2 = year2 + '-' + month2 + '-' + day2;
  return t2;
}


function getNextDay(dateTemps, days) {
  var dateTemp = dateTemps.split("-");
  var nDate = new Date(dateTemp[1] + '-' + dateTemp[2] + '-' + dateTemp[0]);
  var millSeconds = Math.abs(nDate) + (days * 24 * 60 * 60 * 1000);
  var rDate = new Date(millSeconds);
  var year = rDate.getFullYear();
  var month = rDate.getMonth() + 1;
  if (month < 10) month = "0" + month;
  var date = rDate.getDate();
  if (date < 10) date = "0" + date;
  return (year + "-" + month + "-" + date);
}

const combineCell = (list) => {
  for (let field in list[0]) {
    var k = 0;
    while (k < list.length) {
      list[k][field + 'span'] = 1;
      list[k][field + 'dis'] = false;
      for (var i = k + 1; i <= list.length - 1; i++) {
        if (list[k][field] == list[i][field] && list[k][field] != '') {
          list[k][field + 'span']++;
          list[k][field + 'dis'] = false;
          list[i][field + 'span'] = 1;
          list[i][field + 'dis'] = true;
        } else {
          break;
        }
      }
      k = i;
    }
  }
  return list;
}
