/**
 *  row {object} row
 *  obj key
 * {
 *    num: // key
 *    price: // key
 *    discount_unitPrice  //  key
 *    discount_price      // key
 *    totalPrice          // key
 * }
 *  key key value
 *  that this 
 *  callback 
 *  minus   false
 *  major     
 */
// 
var obj = {
  num: 'goods_num', // key
  price: 'sales_price', // key
  goodsPrice: '', // key   * 
  discount_unitPrice: 'discount_unit_price', //  key
  discount_price: 'discount_price', // key
  totalPrice: 'subtotal_price', // ,
  taxrate: 'taxrate', // 
  revenue_price: 'revenue_price',  // 
  no_revenue_price: 'no_revenue_price',// ,
  BuoyancyFactor: 'BuoyancyFactor',  // ,
}
// key
function computeTotal(row, obj, key, that, extendObj) {
  var { callback, minus = false, major = false } = extendObj ? extendObj : {}
  // 
  if (isNaN(row[key]) || Number(row[key]) < 0) {
    row[key] = 0
    that.$set(row, key, 0)
  }
  // 
  if (getType(obj, key) === 'num') {
    // 
    if (row[obj.price] || row[obj.price] === 0) { //  
      if (obj.goodsPrice && (row[obj.price] || row[obj.price] === 0)) {
        that.$set(row, obj.goodsPrice, (Number(row[key]) * Number(row[obj.price])).toFixed(2))
      }
      if (obj.totalPrice && !obj.goodsPrice && (row[obj.price] || row[obj.price] === 0)) { //   
        that.$set(row, obj.totalPrice, (Number(row[key]) * Number(row[obj.price])).toFixed(2))
      }
    } else {
      // 
      if (obj.num && (Number(row[obj.num]) > 0)) {
        if ((row[obj.goodsPrice] || row[obj.goodsPrice] > 0) && obj.goodsPrice) { // 
          that.$set(row, obj.price, (row[obj.goodsPrice] / Number(row[obj.num])).toFixed(2))
        } else if ((row[obj.totalPrice] || row[obj.totalPrice] === 0) && !obj.goodsPrice) { // 
          if (obj.discount_price && (row[obj.discount_price] || row[obj.discount_price] === 0)) {
            that.$set(row, obj.price, (row[obj.totalPrice] - row[obj.discount_price] / Number(row[obj.num])).toFixed(2))
          } else {
            that.$set(row, obj.price, (row[obj.totalPrice] / Number(row[obj.num])).toFixed(2))
          }
        }
      }
    }
    // 
    if (obj.discount_price && obj.discount_unitPrice && Number(row[obj.num] > 0)) {
      if ((row[obj.discount_unitPrice] || row[obj.discount_unitPrice] === 0) && !major) { //  
        that.$set(row, obj.discount_price, (row[obj.discount_unitPrice] * row[obj.num]).toFixed(2))
      }
      if ((row[obj.discount_price] || row[obj.discount_price] === 0) && major) { //  
        that.$set(row, obj.discount_unitPrice, (row[obj.discount_price] / Number(row[obj.num])).toFixed(2))
      }
    }
  }
  //  * 
  if (getType(obj, key) === 'goodsPrice' && (row[obj.goodsPrice] || row[obj.goodsPrice] === 0)) {
    if (obj.price && Number(row[obj.num] > 0)) {
      // row[obj.price] = (row[key] / Number(row[obj.num])).toFixed(2)
      that.$set(row, obj.price, (row[key] / Number(row[obj.num])).toFixed(2))
    }
  }
  // 
  if (getType(obj, key) === 'price' && (row[obj.price] || row[obj.price] === 0)) { // 
    if (obj.goodsPrice && Number(row[obj.num] > 0)) {
      that.$set(row, obj.goodsPrice, (row[key] * Number(row[obj.num])).toFixed(2))
    }
  }
  // 
  if (getType(obj, key) === 'discount_price') {
    if (Number(row[obj.num] > 0)) { // 
      that.$set(row, obj.discount_unitPrice, (row[key] / Number(row[obj.num])).toFixed(2))
    }
  }
  // 
  if (getType(obj, key) === 'discount_unitPrice') {
    if (Number(row[obj.num]) === 0 || row[obj.num]) { // 
      that.$set(row, obj.discount_price, (row[key] * Number(row[obj.num])).toFixed(2))
    }
  }
  // 
  if (getType(obj, key) === 'totalPrice') {
    if (Number(row[obj.num] > 0)) {
      if (obj.discount_price && (row[obj.discount_price] || row[obj.discount_price] === 0)) {
        that.$set(row, obj.price, ((row[key] - row[obj.discount_price]) / Number(row[obj.num])).toFixed(2))
      } else {
        that.$set(row, obj.price, (row[key] / Number(row[obj.num])).toFixed(2))
      }
    }
    computedTax(obj, row, key)
    return
  }
  // 
  if (getType(obj, key) === 'revenue_price') {
    computedTax(obj, row, key)
    return
  }
  var result = computeAll(row, obj)
  if (result !== null) {
    if (result < 0 && !minus) {
      that.$set(row, obj.discount_price, 0)
      that.$set(row, obj.discount_unitPrice, 0)
      computeTotal(row, obj, key, that, callback) //   
    } else {
      if (obj.totalPrice) {
        if (getType(obj, key) === 'totalPrice') {
          that.$set(row, obj.totalPrice, Number(result))
        } else {
          that.$set(row, obj.totalPrice, Number(result).toFixed(2))
        }
      }
    }
  } else {
    if (obj.totalPrice) {
      that.$set(row, obj.totalPrice, '')
    }
  }
  computedTax(obj, row, key)
  callback && callback() // 
}
// 
function computedTax(obj, row, key) {
  //    =/1+*=02
  var totalPrice
  if (obj.goodsPrice) {
    totalPrice = Number(row[obj.goodsPrice])
  }
  if (obj.totalPrice) {
    totalPrice = Number(row[obj.totalPrice])
  }
  if (obj.BuoyancyFactor && Number(row[obj.BuoyancyFactor]) !== 0 && !isNaN(row[obj.BuoyancyFactor])) { // 
    if (obj.discount_price && !isNaN(row[obj.discount_price])) {
      totalPrice = (totalPrice + Number(row[obj.discount_price])) * Number(row[obj.BuoyancyFactor])
    } else {
      totalPrice = totalPrice * Number(row[obj.BuoyancyFactor])
    }
  }
  if (obj.taxrate && obj.revenue_price && !isNaN(totalPrice)) {
    if (getType(obj, key) !== 'revenue_price' && !isNaN(row[obj.taxrate])) {
      var taxrate = row[obj.taxrate] / 100
      row[obj.revenue_price] = (totalPrice / (1 + taxrate) * taxrate).toFixed(2)
    }
    if (isNaN(row[obj.revenue_price])) {
      row[obj.revenue_price] = 0
    }
    if (getType(obj, 'no_revenue_price')) {
      row[obj.no_revenue_price] = (totalPrice - Number(row[obj.revenue_price])).toFixed(2)
    }
  }
}
// key
function getType(obj, key) {
  for (var i in obj) {
    if (obj[i] === key) {
      return i
    }
  }
  console.log('  key')
}
//  
function computeAll(row, obj) {
  var result = null
  if (obj.goodsPrice && (row[obj.goodsPrice] || row[obj.goodsPrice] === 0)) {
    if ((row[obj.discount_price] || row[obj.discount_price] === 0)) {
      result = row[obj.goodsPrice] - row[obj.discount_price]
    } else {
      result = row[obj.goodsPrice]
    }
  } else if ((row[obj.num] || row[obj.num] === 0) && (row[obj.price] || row[obj.price] === 0)) {
    if ((row[obj.discount_price] || row[obj.discount_price] === 0)) {
      result = row[obj.num] * row[obj.price] - row[obj.discount_price]
    } else {
      result = row[obj.num] * row[obj.price]
    }
  }
  return result
}
// 
function isInt(rule, value, callback) {
  if (isNaN(Number(value)) || parseInt(value) !== Number(value)) {
    var msg = rule.label ? rule.label : ''
    callback(new Error(msg + ''))
  }
  callback()
}
//   type   min()  max()  greater()  less()
function toFixedTwo(row, key, obj = {}) {
  var { type, min, max, greater, less, message } = obj
  if (isNaN(row[key]) || row[key] === '') {
    row[key] = ''
    return
  } else {
    row[key] = Number(row[key]).toFixed(2)
  }
  if (type === 'Positive' && Number(row[key]) < 0) {
    Message.warning('')
    row[key] = ''
    return
  }
  if (type === 'Integer') {
    row[key] = Number(row[key]).toFixed(0)
  }
  if (type === 'PositiveInteger') {
    row[key] = Number(Math.abs(row[key])).toFixed(0)
  }
  if (type === 'positive' && Number(row[key]) <= 0) {
    Message.warning('')
    row[key] = ''
    return
  }
  if (min !== undefined && Number(row[key]) < min) {
    if (message) {
      Message.warning(message)
    } else {
      Message.warning('' + min + '')
    }
    row[key] = min
    return
  }
  if (max !== undefined && Number(row[key]) > max) {
    if (message) {
      Message.warning(message)
    } else {
      Message.warning('' + max + '')
    }
    row[key] = ''
    return
  }
  if (greater !== undefined && Number(row[key]) <= greater) {
    Message.warning('' + greater + '')
    row[key] = ''
    return
  }
  if (less !== undefined && Number(row[key]) >= less) {
    Message.warning('' + less + '')
    row[key] = ''
    return
  }
}
