function limitInput(obj, name, value) {
  var result =
    ("" + value) // 
      .replace(/[^\d^\.]+/g, "") // 
      .replace(/^0+(\d)/, "$1") // 00
      .replace(/^\./, "0.") //  0. 
      .match(/^\d*(\.?\d{0,2})/g)[0] || ""; //  02
  console.log(result)
  obj[name] = result
}
function deduplication(arr, key) {
  const result = [];
  arr.forEach(element => {
    const isIn = result.find(item => item[key] === element[key]);
    if (!isIn) {
      result.push(element);
    }
  });
  return result;
}
