function getAllTime(time) {
  if (!time) {
    return ''
  }
  if (isNaN(time - 0)) {
    return time
  }
  var result = new Date(time - 0)
  var year = result.getFullYear()
  var month = result.getMonth() + 1
  var day = result.getDate()
  var hours = result.getHours()
  var minuter = result.getMinutes()
  var s = result.getSeconds()
  return `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day} ${hours < 10 ? '0' + hours : hours}:${minuter < 10 ? '0' + minuter : minuter}:${s < 10 ? '0' + s : s}`
}
// 
function getTime(time) {
  if (!time) {
    return ''
  }
  if (isNaN(time - 0)) {
    return time
  }
  var result = new Date(time - 0)
  var year = result.getFullYear()
  var month = result.getMonth() + 1
  var day = result.getDate()
  return `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day}`
}
// 
const deepCopy = (obj) => {
  var result = Array.isArray(obj) ? [] : {}
  for (var i in obj) {
    if (Object.prototype.toString.call(obj[i]) === "[object Object]" || Object.prototype.toString.call(obj[i]) === "[object Array]") {
      result[i] = deepCopy(obj[i])
    } else {
      result[i] = obj[i]
    }
  }
  return result
}
// 
// source(model) { keyvalue} obj(params) {key: value}
const filter = (source, obj) => {
  var result = deepCopy(source)
  for (var i in result) {
    if (typeof (result[i]) === "object") {
      if ((obj[i] !== undefined && obj[i] !== null)) {
        if (result[i] && result[i].formatter === 'Int') {
          result[i] = isNaN(parseInt(obj[i])) ? 0 : parseInt(obj[i])
        }
      } else {
        result[i] = result[i].default
      }
    } else {
      result[i] = (obj[i] !== undefined && obj[i] !== null) ? obj[i] : result[i]
    }
  }
  return result
}
//  delete
const dealParams = (params) => {
  var result = {}
  for (var i in params) {
    if (params[i] !== undefined && params[i] !== null && params[i] !== '') {
      result[i] = params[i]
    }
  }
  return result
}
// 
const filterTableColumn = (arr) => {
  var arrCopy = deepCopy(arr), result = [];
  arrCopy.forEach((item) => {
    item.editRender = null
    if (item.type !== 'checkbox') {
      result.push(item)
    }
  })
  return result
}
