function getNumber(a) {
  return isNaN(a) ? 0 : Number(a)
}
function priceFormatNone(num, precision = 2, separator = ",") { //
  if (typeof (precision) !== 'number') {
    precision = 2
  }
  if (typeof (separator) !== 'string') {
    separator = ","
  }
  var parts;
  if (!isNaN(parseFloat(num)) && isFinite(num)) {
    num = Number(num);
    num = (typeof precision !== "undefined" ?
      num.toFixed(precision) :
      num
    ).toString();
    parts = num.split(".");
    parts[0] = parts[0]
      .toString()
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + (separator || ","));
    return parts.join(".");
  } else if (num == "") {
    return "";
  }
  return num;
}
// 
function sum(box, value) {
  var result = 0
  if (box.length >= 1) {
    if (value) {
      result = box.reduce((a, b) => {
        return getNumber(a[value]) + getNumber(b[value])
      })
    } else {
      result = box.reduce((a, b) => {
        return getNumber(a) + getNumber(b)
      })
    }
  }
  if (typeof (result) === 'object') {
    result = result[value]
  }
  return getNumber(result)
}
const confirm = Throttle(confirmFunction, 1000)
function getWeekYear() {
  var today = new Date();
  var firstDay = new Date(today.getFullYear(), 0, 1);
  var dayOfWeek = firstDay.getDay();
  var spendDay = 1;
  if (dayOfWeek != 0) {
    spendDay = 7 - dayOfWeek + 1;
  }
  firstDay = new Date(today.getFullYear(), 0, 1 + spendDay);
  var d = Math.ceil((today.valueOf() - firstDay.valueOf()) / 86400000);
  var result =
    String(Math.ceil(d / 7) + 1).length == 2 ?
      Math.ceil(d / 7) + 1 :
      `0${Math.ceil(d / 7) + 1}`;
  var year = today
    .getFullYear()
    .toString()
    .substring(2);
  return `${result}${year}`;
}
